.friends__item {
  display: flex;
  align-items: center;
  box-shadow: 0 0 10px #c6cccced;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 10px;
  background-color: white;
}

.friends__status {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 20px;
}

.true {
  background-color: green;
}

.false {
  background-color: red;
}

.avatar {
  width: 100px;
  margin-right: 10px;
  background-color: #9dadaded;
  border-radius: 10px;
}

.friends__name {
  font-weight: 500;
  font-size: 30px;
  margin-bottom: 15px;
  color: #1d1c1c;
}
