table {
  margin-top: 30px;
  box-shadow: 0 10px 10px #c6cccced;
  width: 370px;
  border-spacing: 0;
}

thead tr {
  margin-bottom: 5px;
  padding: 10px;
  background-color: rgb(0, 119, 255);
}

table th {
  font-weight: 800;
  font-size: 20px;
  margin-bottom: 5px;
  color: #524a4d;
}

table td {
  padding: 10px;
  width: inherit;
  font-weight: 700;
  font-size: 15px;
  color: #524a4d;
  display: flex;
  justify-content: center;
}

table tr {
  display: flex;
  justify-content: space-around;
  border-bottom: solid 1px #c6cccce6;
}

table tr:not(:last-child) {
  margin-bottom: 5px;
  background-color: white;
}

table tr:nth-child(2n) {
  background-color: rgb(148, 148, 148);
}
