.profile {
  width: 1000px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  width: 370px;
  box-shadow: 0 0 10px #c6cccced;
  border-radius: 10px;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: inherit;
  border-bottom: #79767798 solid 1px;
}

.avatar {
  width: 150px;
  box-shadow: 0 0 3px 0px #0000004d;
  border-radius: 50%;
  background-color: #9dadaded;
  padding: 20px 10px 0px 10px;
  margin-bottom: 30px;
}

.name {
  font-weight: 500;
  font-size: 30px;
  margin-bottom: 15px;
  color: #1d1c1c;
}

.tag {
  font-weight: 500;
  font-size: 20px;
  color: #7a8181;
  margin-bottom: 15px;
}

.location {
  font-weight: 700;
  font-size: 20px;
  color: #7a8181;
  margin-bottom: 40px;
}

.stats {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: auto;
  padding: 0;
  width: inherit;
  background-color: #d7ebea7c;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

.stats > li {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: calc(100% / 3);
  padding: 30px 0;
}

.first_item,
.second_item {
  border-right: #79767798 solid 1px;
}

.label {
  margin-right: 3px;
  font-weight: 700;
  font-size: 20px;
  color: #524a4d8b;
  margin-bottom: 5px;
}

.quantity {
  font-weight: 700;
  font-size: 20px;
  color: #1d1c1c;
}
